@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap);
.App{display:flex;align-items:center;justify-content:center;height:100vh}.App .productsGrid{display:inline-flex;align-items:center;justify-content:center;grid-gap:6rem;gap:6rem;background-color:#e9ebee;width:100%;height:100%}

.addButton{height:48px;background:#40b25c;border-radius:5px;border:none;cursor:pointer;font-style:normal;font-weight:bold;font-size:16px;line-height:24px;letter-spacing:-0.005em;text-transform:uppercase;color:#ffffff;transition:0.5s}.addButton:hover{background:#1c6c3e}.isActiveAddButton{height:48px;background:#a3f9b9;border-radius:5px;border:none;cursor:pointer;font-style:normal;font-weight:bold;font-size:16px;line-height:24px;letter-spacing:-0.005em;text-transform:uppercase;color:#1c1c1c;transition:0.5s}.isActiveAddButton:hover{background:#7feb9a}

.oldPrice{font-style:normal;font-weight:500;font-size:14px;line-height:20px;margin:0;letter-spacing:-0.005em;-webkit-text-decoration-line:line-through;text-decoration-line:line-through;color:#b5b5b6}.price{font-style:normal;font-weight:600;font-size:20px;line-height:24px;margin:4px 0;letter-spacing:-0.005em;color:#da4b4f}.parcelamento{font-style:normal;font-weight:500;font-size:12px;line-height:16px;margin:0;letter-spacing:-0.005em;color:#848587}.parcelamento strong{color:#1c1c1c;font-weight:normal}.installment{font-style:normal;font-weight:500;font-size:12px;line-height:16px;margin:0;letter-spacing:-0.005em;color:#848587}.installment strong{color:#1c1c1c;font-weight:normal}

.item{display:flex;flex-direction:column;justify-content:center;padding:32px;width:304px;height:568px;background-color:#ffffff;border-radius:8px;box-shadow:0px 8px 24px rgba(0,0,0,0.08)}

.favorite{position:absolute;top:0;right:1.8rem;display:flex;align-items:center;justify-content:center;width:48px;height:48px;border-radius:50%;background-color:#f2f3f6;cursor:pointer;transition:0.5s}.favorite:hover{background-color:#fee5ec}.favorite:hover svg path{stroke:#da4b4f}.isActiveFavoriteButton{position:absolute;top:0;right:1.8rem;display:flex;align-items:center;justify-content:center;width:48px;height:48px;border-radius:50%;background-color:#da4b4f;cursor:pointer;transition:0.5s}.isActiveFavoriteButton svg path{fill:#ffffff;stroke:#ffffff}.isActiveFavoriteButton:hover{background-color:#c22539}

.itemHeader{display:flex;position:relative;flex-direction:column;align-items:center;justify-content:center}.itemHeader .title{font-style:normal;font-weight:500;font-size:14px;line-height:20px;color:#1c1c1c}

body{margin:0;font-family:'Inter', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

